import React from 'react'
import PropTypes from 'prop-types'
import { AnzGrid } from '@anz/grid'

import {
  ProgressContainer,
  ProgressList,
  ProgressListItem,
  ProgressCircle,
  ProgressText,
  ProgressStep,
  ProgressLink,
  TickIcon,
  StepNumber
} from './styles'

const ProgressStepWrapper = ({ href, children }) =>
  href ? (
    <ProgressLink href={href}>{children}</ProgressLink>
  ) : (
    <span>{children}</span>
  )

const AnzProgressBar = props => (
  <ProgressContainer data-test-id='progress-bar'>
    <AnzGrid fluid maxWidth={props.maxWidth}>
      <ProgressList>
        {props.progressSteps.map((progressStep, Key) => (
          <ProgressListItem
            key={Key}
            aria-current={progressStep.active ? 'page' : undefined}
          >
            <ProgressStepWrapper href={progressStep.href}>
              <ProgressStep>
                <ProgressCircle
                  data-test-id={`step-circle-${Key + 1}`}
                  data-test-id-active={progressStep.active}
                  data-test-id-tick={
                    progressStep.completed && !progressStep.active
                  }
                  active={progressStep.active}
                  completed={progressStep.completed}
                >
                  {progressStep.completed && !progressStep.active ? (
                    <TickIcon />
                  ) : (
                    ''
                  )}
                  <StepNumber>{Key + 1}</StepNumber>
                </ProgressCircle>
                <ProgressText
                  active={progressStep.active}
                  completed={progressStep.completed}
                >
                  {progressStep.title}
                </ProgressText>
              </ProgressStep>
            </ProgressStepWrapper>
          </ProgressListItem>
        ))}
      </ProgressList>
    </AnzGrid>
  </ProgressContainer>
)

AnzProgressBar.propTypes = {
  /** An array of breadcrumbs to show in the progress bar. */
  progressSteps: PropTypes.arrayOf(
    PropTypes.shape({
      /** The text displayed at laptop and desktop widths in the progress step. */
      title: PropTypes.string.isRequired,
      /** A URL to link to from the progress step if required. */
      href: PropTypes.string,
      /** Visually mark the step as complete. */
      completed: PropTypes.bool,
      /** Visually mark the step as active. */
      active: PropTypes.bool
    })
  ).isRequired,
  /** The maximum width of the underlying grid within the masthead child components, offsets the icons and any content while keeping the background spanning 100% of its container. */
  maxWidth: PropTypes.oneOf([
    '320',
    '480',
    '768',
    '980',
    '1164',
    '1366',
    'auto'
  ])
}

export default AnzProgressBar
